video {
    object-fit: cover;
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: -1;
  }

.circular_image {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  overflow: hidden;
  margin-bottom: 80px;
  display:inline-block;
  vertical-align:middle;
}
.circular_image img{
  width:100%;
}

.hero-container {
    background-image: url('../../assets/background1.JPG');
    background-position: center;
    background-repeat:  no-repeat;
    background-size: cover; 
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.2);
    object-fit: contain;
  }
  
  .hero-container > h1 {
    color: #fff;
    font-size: 60px;
    margin-top: -90px;
  }

  
  
  .hero-container > p {
    margin-top: 5px;
    color: #fff;
    font-size: 32px;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
      'Lucida Sans', Arial, sans-serif;
  }
  
  .hero-btns {
    margin-top: 32px;
  }
  
  .hero-btns .btn {
    margin: 2px;
  }
  
  .fa-play-circle {
    margin-left: 4px;
  }
  
  @media screen and (max-width: 960px) {
    .hero-container > h1 {
      font-size: 70px;
      margin-top: -150px;
    }
  }
  
  @media screen and (max-width: 768px) {
    .hero-container > h1 {
      font-size: 30px;
      margin-top: -100px;
    }
    .hero-container > .circular_image{
      width: 180px;
      height: 180px;
      border-radius: 50%;
      overflow: hidden;
      margin-bottom: 90px;
      display:inline-block;
      vertical-align:middle;
    }
  
    .hero-container > p {
      font-size: 20px;
    }
  
    .btn-mobile {
      display: block;
      text-decoration: none;
    }
  
    .btn {
      width: 100%;
    }
  }
  @media screen and (max-width:375) {
    .hero-container > p {
      font-size: 15px;
    }
  
    
  }